.gradient {
  background-image: linear-gradient(70deg, rgb(146, 184, 202), white);
}

#h1 {
  color: rgb(2, 2, 85);
  justify-content: space-between;
  flex-direction: row;
  text-align: center;
}

.profile {
  border: linen 8px solid;
  height: 12rem;
  border-radius: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.example {
    text-align: center;
    background: linear-gradient(
      to right, 
      rgb(2, 2, 85), 
      hsl(204 100% 59%)
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

  }
  